
@font-face {
  font-family: DINBold;
  src: url('./assets/font/DINBold.woff');
}
html {
  font-size: .9em;
}
html,body {
  font-family: DINBold, Futura, MeiHei, Helvetica, Arial, sans-serif;
  margin: 0;
  height: 100%;
  width: 100%;
  /*overflow: hidden;*/
}

/*
  * 屏幕高大于500px用以下css
  */
@media screen and (min-height:600px) {
#app {
      width: 100%;
      height: 100%;
      overflow: hidden;
}
}

/*  
  * 屏幕高小于500px用以下css
  */
@media screen and (max-height:600px) {
#app {
      width: 100%;
      height: 100%;
}
}
a {
  text-decoration: none;
  color: #000;
}
.wrap {
  will-change: transform;
  transform: translateZ(0);
  padding-top: 8vh;
}
#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
a {
  text-decoration: none;
  color: #000;
}
div,a,img {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select:none;
}
.wrap {
  will-change: transform;
  transform: translateZ(0);
  padding-top: 8vh;
}
.hide {
  animation: dropdown 1s;
  transform: translateY(-92vh);
}
.show {
  animation: rollback 1s;
  transform: translateY(0);
}
@keyframes dropdown {
0% {
    transform: translateY(0);
}
100% {
    transform: translateY(-92vh);
}
}
@keyframes rollback{
0% {
    transform: translateY(-92vh);
}
100% {
    transform: translateY(0);
}
}




