
.album-wrap {
  width: 100%;
  height: 92vh;
  box-shadow: 5px 1px 0 rgba(0,0,0,.1), inset 0 0 0 rgba(0,0,0,.1);
}
.album {
  width: 100%;
  padding: 0 10%;
  margin-top: 30vh;
  box-sizing: border-box;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
}
.album-box {
  width: 30vw;
  height: 18vw;
  text-align: center;
  font-size: 1.8rem;
}
.album-img-wrap {
  width: 18vw;
  height: 18vw;

  display: inline-block;
  background-color: #000;
  opacity: 0.9;
  animation: showImg 2.6s;
  box-shadow: 0 20px 20px -10px rgba(0, 0, 0, 0.19);
}
.album-img {
  width: 18vw;
  display: block;
}
.album-box p {
  text-transform: uppercase;
  font-size: 16px;
  text-align: center;
  font-weight: 600;
  animation: showImg 4s;
}
@media screen and (min-width:800px) {
.album-img {
    opacity: 0.1;
    transition: opacity 1.5s;
}
.album-img:hover {
    opacity: 1;
}
}
@media screen and (max-width:800px) {
.album-box p {
    font-size: 14px;
}
}
@keyframes showImg {
0% {
    opacity: 0;
}
50% {
    opacity: 0;
}
100% {
    opacity: 0.9;
}
}



